'use client';

import { useDAWStore } from '@/store/useDAWStore';
import { modalClient } from '@/lib/modal-client';
import { Fader } from '@/components/daw/Fader';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';
import { Download, Upload, Play, Square, Settings2, Mic2, Loader2 } from 'lucide-react';

export default function Studio() {
    const faders = useDAWStore((state) => state.faders);
    const isTraining = useDAWStore((state) => state.isTraining);
    const setTrainingStatus = useDAWStore((state) => state.setTrainingStatus);
    const setFaderValue = useDAWStore((state) => state.setFaderValue);

    const handleTrainVocal = async () => {
        setTrainingStatus(true);
        try {
            // Call Modal.com remote function
            const response = await modalClient.process_vocal_cloning();

            if (response.success) {
                // Modal responded! Automate fader moves to the professional mix
                setFaderValue('vocal', response.mixParameters.vocalLevel); // 85
                setFaderValue('beat', response.mixParameters.beatLevel);  // 75

                // Drop other stems slightly to make room
                setFaderValue('bass', 65);
                setFaderValue('fx', 50);
            }
        } catch (error) {
            console.error("Modal training failed:", error);
        } finally {
            setTrainingStatus(false);
        }
    };

    return (
        <div className="flex flex-col h-full gap-6 max-w-7xl mx-auto w-full">
            {/* Top Header Row */}
            <header className="flex items-end justify-between border-b border-silver-dark/20 pb-4">
                <div>
                    <h1 className="text-4xl justify-center font-black tracking-tighter text-transparent bg-clip-text bg-gradient-to-r from-white to-silver-dark flex items-center gap-4 drop-shadow-[0_0_10px_rgba(255,107,0,0.3)]">
                        DA GRABA
                        <span className="text-primary font-normal text-3xl font-mono mix-blend-screen relative flex items-center gap-2">
                            STUDIO
                            {isTraining && (
                                <Loader2 size={24} className="text-primary animate-spin shadow-[0_0_15px_rgba(255,107,0,0.8)] rounded-full" />
                            )}
                        </span>
                    </h1>
                    <p className="text-silver-dark text-sm mt-1 tracking-widest font-mono">
                        {isTraining ? 'MODAL.COM: process_vocal_cloning() IN PROGRESS...' : 'MIXING CONSOLE - SESSION 01'}
                    </p>
                </div>

                <div className="flex gap-4">
                    {/* The new AI Train Vocal button */}
                    <Button
                        variant={isTraining ? 'active' : 'default'}
                        size="sm"
                        className={`gap-2 ${isTraining ? 'animate-pulse' : 'bg-gradient-to-r from-primary to-orange-600 shadow-xl'}`}
                        onClick={handleTrainVocal}
                        disabled={isTraining}
                    >
                        {isTraining ? <Loader2 size={14} className="animate-spin" /> : <Mic2 size={14} />}
                        {isTraining ? 'Entrenando y Mezclando...' : 'Entrenar Voz (Modal AI)'}
                    </Button>

                    <Button variant="outline" size="sm" className="gap-2">
                        <Upload size={14} /> Import
                    </Button>
                    <Button variant="outline" size="sm" className="gap-2 text-silver-dark">
                        <Download size={14} /> Export
                    </Button>
                </div>
            </header>

            {/* Main Console Area */}
            <div className="flex-1 flex flex-col justify-end min-h-0 bg-neutral-900/40 rounded-2xl p-6 border border-white/5 shadow-2xl relative overflow-hidden backdrop-blur-md">

                {/* Subtle background decoration */}
                <div className="absolute top-0 right-0 w-[500px] h-[500px] bg-primary/5 rounded-full blur-[100px] pointer-events-none transition-opacity duration-1000" style={{ opacity: isTraining ? 0.3 : 0.05 }} />

                <div className="flex-1">
                    {/* Waveform / Analysis area placeholder */}
                    <Card className={`h-full bg-carbon/40 mb-6 flex flex-col justify-center items-center border-dashed border-silver-dark/20 relative overflow-hidden transition-all duration-1000 ${isTraining ? 'border-primary/50 shadow-[0_0_50px_rgba(255,107,0,0.1)]' : ''}`}>
                        <div className="absolute inset-0 bg-[url('https://www.transparenttextures.com/patterns/carbon-fibre.png')] opacity-10" />
                        <Settings2 size={48} className={`text-primary/20 mb-4 transition-transform duration-[3000ms] ${isTraining ? 'rotate-180 scale-125 text-primary/50' : ''}`} />
                        <p className="text-silver-dark font-mono text-xs tracking-widest">
                            {isTraining ? 'MODAL SDK: PARSING VOCAL STEMS...' : 'WAVEFORM ANALYZER / MASTER BUSS'}
                        </p>
                    </Card>
                </div>

                {/* Console Fader Bank */}
                <div className="flex justify-center gap-4 bg-carbon/80 p-6 rounded-xl border border-white/10 shadow-[0_-10px_40px_rgba(0,0,0,0.5)] z-10 transition-shadow duration-1000" style={{ boxShadow: isTraining ? '0 -10px 40px rgba(255,107,0,0.2)' : '' }}>

                    <div className="flex gap-2">
                        {/* 4 Mapped Faders: vocal, beat, bass, fx */}
                        {faders.map((fader) => (
                            <Fader key={fader.id} id={fader.id} />
                        ))}
                    </div>

                    <div className="w-[1px] bg-gradient-to-b from-transparent via-silver-dark/30 to-transparent mx-4" />

                    {/* Master Section */}
                    <div className="flex flex-col items-center gap-4 p-4 rounded-xl glass-panel w-32 border-primary/20 shadow-[0_0_20px_rgba(255,107,0,0.05)] bg-gradient-to-b from-carbon to-neutral-900">
                        <div className="text-xs font-black text-primary tracking-widest">MASTER</div>

                        {/* Master specific tools could go here */}
                        <div className="flex gap-2 mt-2 w-full">
                            <Button size="icon" variant="outline" className="h-8 flex-1 text-green-500 hover:text-green-400 hover:bg-green-500/10">
                                <Play size={12} fill="currentColor" />
                            </Button>
                            <Button size="icon" variant="outline" className="h-8 flex-1 text-red-500 hover:text-red-400 hover:bg-red-500/10">
                                <Square size={12} fill="currentColor" />
                            </Button>
                        </div>

                        <div className="flex-1 mt-6 w-full relative group">
                            {/* Visual styling for master fader slot */}
                            <div className="absolute inset-x-0 inset-y-8 bg-black border border-white/5 rounded-full w-4 mx-auto" />

                            {/* Real fader (disabled for visual structure) */}
                            <div className="h-full flex items-center justify-center pointer-events-none opacity-50 relative z-10 w-full pl-3">
                                <div className={`w-8 h-4 bg-primary rounded shadow shadow-primary blur-[2px] transition-all duration-1000 ${isTraining ? 'animate-pulse' : ''}`} />
                            </div>
                        </div>
                    </div>

                </div>

            </div>
        </div>
    );
}
